@import '../../../../../assets/css/var.scss';
@import '../../../../../assets//css/theme';
.increaseTokenInfo {
  display: inline-flex;
  align-items: center;
}
.token_logo {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #eee;
  background: #fff;
  &:nth-child(2) {
    transform: translateX(-6px);
  }
}
.token_name {
  margin-left: 4px;
  margin-right: 10px;
  font-size: 16px;
  font-family: Poppins;
  font-weight: 600;
  color: #fff;
}

.fee_tetx {
  padding: 0 6px;
  line-height: 20px;
  font-size: 12px;
  font-family: Poppins;
  font-weight: 600;
  background: #26193d;
  border-radius: 20px;
  color: #a29cbc;
}

@include mobile-only {
  .token_name {
    font-size: 14px;
    margin-right: 6px;
  }
}
