@import '../../../assets/css/var';
@import '../../../assets/css/mixins';

.back {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  font-family: Poppins;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #a29cbc;
  &:hover {
    color: #fff;
  }

  img {
    display: block;
    width: 16px;
    height: 16px;
  }
  span {
    margin-left: 8px;
  }
}
