@import '../../../../../assets/css/theme';
@import '../../../../../assets/css/mixins';

.preset_btn {
  display: inline-block;
  border-radius: 30px;
  font-size: 14px;
  font-family: Poppins;
  font-weight: 600;
  line-height: 20px;
  padding: 5px 10px;
  cursor: pointer;
  background: transparent;
  color: #a29cbc;
  &:hover {
    background: #6400ff;
    color: #fff;
  }
}

@include mobile-only {
  .preset_btn {
    font-size: 12px;
  }
}
