@import '../../../../../assets/css/var.scss';
@import '../../../../../assets//css/theme';
@import '../../../../../assets/css/mixins';

.v3-add-page {
  .v3-add-page-swap {
    // display: flex;
    // justify-content: center;
    // align-items: flex-start;
  }
  .swap-arrow {
    width: 46px;
    height: 46px;
    cursor: pointer;
    &.disable {
      opacity: 0.3;
      pointer-events: none;
    }
    @include darkThemeOnly {
      background: url('../../../../../assets/images/swap-double-arrow.png') 100%/100% no-repeat;
    }
    @include lightThemeOnly {
      transform: rotate(90deg);
      background: url('../../../../../assets/images/arrow/arrow_double_dark_.png') 100% / 100% no-repeat;
    }
  }
  .v3-add-page-swap-inline {
    > div:first-child {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      margin-bottom: 30px;
      > div {
        width: calc(50% - 40px);
      }
      > div:nth-child(1) {
        margin-right: 80px;
      }
    }
  }
  &-header {
    color: $color-text-default;
    font-size: 15px;
    margin-bottom: 15px;
    a,
    a:link,
    a:visited {
      color: $color-text-default;
    }
  }
  .setting-line {
    margin: 15px 0;
    > div:nth-child(1) {
      color: $color-text-default;
      opacity: 0.23;
      font-size: 14px;
    }
    .sun-swap-link-3 {
      color: $color-a;
      font-size: 12px;
      cursor: pointer;
    }
  }
  .current-price {
    line-height: 14px;
    &-label {
      color: $color-text-default-3;
      font-size: 12px;
    }
    &-text {
      font-weight: bold;
      color: $color-text-default;
      font-size: 12px;
    }
  }
  .add-liquidity-error-orange {
    font-size: 12px;
    font-family: Poppins;
    color: #ffc400;
    line-height: 16px;
    margin-top: 10px;
  }
  .title-mt {
    margin-top: 30px;
  }
}

.v3-increase-infoBox {
  background: #f00;
}

@media screen and (max-width: 768px) {
  .v3-add-page {
    .v3-add-page-swap {
      display: block;
      > div {
        width: 100%;
        margin-right: 0;
      }
    }

    .swap-arrow {
      width: 30px;
      height: 30px;
    }

    .v3-add-page-swap-inline {
      > div:first-child {
        display: block;
        margin-bottom: 20px;
        > div {
          width: 100%;
          margin-right: 0;
          &:first-child {
            margin-bottom: 20px;
          }
        }
      }
    }
    .title-mt,
    .v3-set-price-title {
      // margin-top: 20px;
    }
  }
}
.v3-charts-zooms {
  position: absolute;
  top: 32px;
  right: 0;
}
