@import '../../../../../assets/css/theme';
@import '../../../../../assets/css/mixins';

.v3-add-page {
  .swap-block {
    .lock-block-wrap {
      padding-left: 30px;
      flex: 1;
      width: 0;
    }
    .lock-block {
      display: flex;
      // justify-content: center;
      align-items: center;
      img {
        display: block;
        width: 30px;
        height: 30px;
      }
      .lock-block-tip {
        flex: 1;
        width: 0;
        margin-left: 10px;
        font-family: Poppins;
        font-weight: 500;
        font-size: 12px;
        line-height: 18px;
        color: #a7a2c9;
        span {
          display: inline-block;
          &:first-child {
            white-space: nowrap;
          }
        }
      }
    }
    .lock-block-tip2 {
      display: none;
    }
    .input-bottom-usd {
      font-size: 14px;
      font-family: Helvetica;
      color: $color-text-default;
      opacity: 0.7;
    }
  }
}

@include mobile-only {
  .v3-add-page {
    .swap-block {
      .lock-block-wrap {
        flex: 1;
        padding-left: 0;
      }
      .lock-block {
        justify-content: flex-end;
        img {
          width: 12px;
          height: 12px;
        }
        .lock-block-tip {
          display: none;
        }
        .lock-block-tip2 {
          display: inline-flex;
          align-items: center;
          span {
            margin: 0 6px;
            color: #524b71;
          }
          .ant-tooltip-content {
            width: 210px;
          }
        }
      }
    }
    .sunPoolBox {
      border-radius: 10px;
      background: #1b0d31;
      .sunPoolBox-left {
        margin-bottom: 0;
        border-radius: 0;
        background: transparent;
      }
      .sunPoolBox-right {
        margin-top: 0;
        border-radius: 0;
        background: transparent;
      }
    }
  }
  .v3-add-page-increase {
    .sunPoolBox {
      border-radius: 0;
      background: transparent;
      .sunPoolBox-left {
        margin-bottom: 20px;
        border-radius: 10px;
        background: #1b0d31;
      }
      .sunPoolBox-right {
        border-radius: 10px;
        background: #1b0d31;
      }
    }
  }
}
