@import '../../../../../assets/css/var.scss';
@import '../../../../../assets/css/theme';

$active-bg: #722fff;

.fee-selector {
  &-item {
    position: relative;
    color: #a7a2c9;
    border-radius: 6px;
    padding: 14px 10px 20px;
    background: #26193d;
    cursor: pointer;
    height: 100%;
    display: flex;
    flex-direction: column;
    @include mobile-only {
      padding: 10px;
    }
    &:hover {
      color: #fff;
      background: #33254e;
    }
    &-active {
      color: #fff;
      background: linear-gradient(90.44deg, #6577ff 0%, #6400ff 99.25%);
      box-shadow: 0px -4px 0px 0px #0901174d inset;
      cursor: default;
      &:hover {
        background: linear-gradient(90.44deg, #6577ff 0%, #6400ff 99.25%);
        box-shadow: 0px -4px 0px 0px #0901174d inset;
      }
    }
    &-disabled {
      opacity: 0.7;
      cursor: no-drop;
    }
    &-percent {
      margin-bottom: 10px;
      font-family: Poppins-Bold;
      font-weight: 700;
      font-size: 20px;
      line-height: 30px;
      @include mobile-only {
        font-size: 16px;
        line-height: 20px;
      }
    }
    &-desc {
      font-family: Poppins;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
    }
    &-no-create {
      font-size: 14px;
      color: $color-text-default-7;
      line-height: 18px;
      margin-top: auto;
    }
  }
}
.mobile-content {
  .fee-selector-item {
    padding: 10px;
    &:hover {
      box-shadow: none;
    }
    padding: 10px;
    &-percent {
      font-size: 16px;
    }
    &-desc,
    &-no-create {
      font-size: 12px;
    }
  }
}
