@import '../../../../../assets/css/var.scss';
@import '../../../../../assets//css/theme';

.v3-range-selector {
  display: flex;
  justify-content: space-between;
}
.v3-step-counter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  //width: 220px;
  &-wrap {
    background: #26193d;
    border: 1px solid #26193d;
    border-radius: 10px;
    padding: 10px;
    &-active {
      border: 1px solid rgba(255, 255, 255, 0.2);
    }
  }
  &-title {
    margin-bottom: 10px;
    font-family: Poppins;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    color: #a29cbc;
  }
  &-bottom {
    margin-top: 10px;
    font-family: Poppins;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    color: #6c6686;
  }
  .base-btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 6px;
    border-color: #a29cbc;
    color: #a29cbc;
    &:hover {
      background: #443564;
      color: #fff;
    }
  }
}
.v3-step-counter .rate-input-0 {
  font-family: Poppins-Bold;
  font-weight: 700;
  border: none;
  padding-top: 0;
  padding-bottom: 0;
  background: transparent;
  color: #fff;
  height: 29px;
  font-size: 20px;
  line-height: 29px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  &::placeholder {
    color: #6c6686;
  }
}
.base-btn {
  flex-shrink: 0;
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 4px;
  border: 1px solid $color-text-default-3;
  color: $color-text-default;
  text-align: center;
  line-height: 30px;
  cursor: pointer;
  background: transparent;
  padding: 0;
  &-disable {
    cursor: no-drop;
  }
}

@include mobile-only {
  .v3-step-counter {
    .base-btn {
      width: 20px;
      height: 20px;
      svg {
        width: 10px;
        height: 10px;
      }
    }
  }
}
