@import '../../../assets/css/var';
@import '../../../assets/css/mixins';

.sunPoolBox {
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-start;
  font-family: Poppins;
  .sunPoolBox-left {
    z-index: 2;
    position: relative;
    flex: 1;
    padding: 20px;
    width: 0;
    margin-left: 20px;
    background: #1b0d31;
    border-radius: 20px;
    &::before {
      content: '';
      position: absolute;
      inset: 0;
      padding: 1px;
      border-radius: 20px;
      background: linear-gradient(181.14deg, rgba(255, 255, 255, 0.2) 25.9%, rgba(255, 255, 255, 0.05) 76.35%);
      -webkit-mask: linear-gradient(#fff, #fff) content-box, linear-gradient(#fff, #fff);
      -webkit-mask-composite: xor;
      mask-composite: exclude;
      z-index: -1;
    }
  }
  .sunPoolBox-right {
    z-index: 1;
    position: relative;
    flex: 1;
    padding: 20px;
    width: 0;
    background: #1b0d31;
    border-radius: 20px;
    &::before {
      content: '';
      position: absolute;
      inset: 0;
      padding: 1px;
      border-radius: 20px;
      background: linear-gradient(181.14deg, rgba(255, 255, 255, 0.2) 25.9%, rgba(255, 255, 255, 0.05) 76.35%);
      -webkit-mask: linear-gradient(#fff, #fff) content-box, linear-gradient(#fff, #fff);
      -webkit-mask-composite: xor;
      mask-composite: exclude;
      z-index: -1;
    }
  }
  .sunPoolBox-loading {
    // padding: 70px 0;
    .sunPoolBox-loading-title {
      margin-bottom: 40px;
      font-family: Poppins;
      font-weight: 600;
      font-size: 20px;
      line-height: 30px;
      color: #fff;
    }
    .sunPoolBox-loading-tip {
      padding: 10px 0 73px;
      font-family: Poppins;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      color: #a29cbc;
      text-align: center;
    }
    img {
      display: block;
      height: 240px;
      margin: 0 auto;
    }
  }
  .sunPoolBox-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    .sunPoolBox-header-left {
      font-family: Poppins-Bold;
      font-weight: 600;
      font-size: 20px;
      line-height: 30px;
      color: #fff;
    }
  }
  .sunPoolBox-card {
    position: relative;
    margin-bottom: 6px;
    padding: 16px 20px 20px;
    background: #26193d;
    backdrop-filter: blur(10px);
    border-radius: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .sunPool-add-icon {
    z-index: 2;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%);
  }
  .sunPoolBox-tip {
    position: relative;
    margin-top: 20px;
    // padding-right: 19px;
    font-family: Poppins;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: #fff;
    word-break: break-all;
    b {
      font-family: Poppins-Bold;
      font-weight: 900;
      color: #ffc400;
    }
    .safe-amount-set-btn {
      display: inline-block;
      font-family: Poppins-Bold;
      font-weight: 700;
      color: #bfabff;
      cursor: pointer;
    }
    .warning-network-tip {
      color: #ffc400;
    }
  }
  .sunPoolCard-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
    .sunPoolCard-header-l {
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }
    .sunPoolCard-header-view {
      margin-right: 10px;
    }
    .sunPoolCard-header-level {
      margin-left: 5px;
      .sfru-token_level {
        height: 20px;
        line-height: 20px;
      }
    }
  }
  .sunPoolCard-balance-m {
    display: none;
  }
  .sunPoolCard-balance {
    margin-top: 16px;
  }
  .sunPoolCard-balance-inner {
    display: flex;
    .sunPoolCard-balance-inner-l,
    .sunPoolCard-balance-inner-r {
      flex: 1;
    }
    .sunPoolCard-balance-inner-r {
      text-align: right;
      margin-left: 10px;
    }
  }
  .sunPoolCard-area {
    display: flex;
    align-items: center;
    .sunPoolCard-token-select {
    }
    .sunPoolCard-token-input {
      flex: 1;
      width: 0;
    }
  }
  .sunPool-action-btn {
    margin-top: 30px;
  }
  .sunPoolBox-card-rate-toggle {
    z-index: 2;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%);
  }
  .sunPoolBox-submit {
    margin-top: 30px;
    button {
      font-family: Poppins;
    }
  }
  .sunPool-v3-tip {
    margin-top: 10px;
  }
}
.sunPool-zoom-btn {
  color: rgba(255, 255, 255, 0.7);
  &:hover {
    color: rgba(255, 255, 255, 1);
  }
  @include mobile-only {
    color: rgba(255, 255, 255, 1);
  }
}

.sunPool-firstCreateInfo {
  margin-top: 30px;
  .sunPool-firstCreateInfo-title {
    font-family: Poppins;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #ff8700;
  }
  .sunPool-firstCreateInfo-desc {
    margin-top: 10px;
    font-family: Poppins;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #ff8700;
    opacity: 0.5;
  }
}

@include mobile-only {
  .sunPoolBox {
    display: block;
    .sunPoolBox-left {
      width: 100%;
      padding: 10px;
      margin-left: 0;
      margin-bottom: 20px;
      border-radius: 10px;
      &::before {
        border-radius: 10px;
      }
    }
    .sunPoolBox-right {
      width: 100%;
      padding: 10px;
      border-radius: 10px;
      &::before {
        border-radius: 10px;
      }

      .sunPoolBox-loading {
        .sunPoolBox-loading-title {
          margin-bottom: 20px;
          font-size: 16px;
          line-height: 24px;
        }
        .sunPoolBox-loading-tip {
          padding: 5px 0 20px;
          font-size: 12px;
          line-height: 16px;
        }
        img {
          height: 120px;
        }
      }
    }
    .sunPoolBox-header {
      display: none;
    }
    .sunPoolBox-card {
      margin-bottom: 5px;
      padding: 13px 10px 10px;
      border-radius: 6px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    .sunPoolBox-tip {
      position: relative;
      margin-top: 10px;
      // padding-right: 19px;
      font-family: Poppins;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      color: #fff;
      word-break: break-word;
      b {
        font-family: Poppins-Bold;
        font-weight: 900;
        color: #ffc400;
      }
      .safe-amount-set-btn {
        display: inline-block;
        font-family: Poppins-Bold;
        font-weight: 700;
        color: #bfabff;
        cursor: pointer;
      }
    }
    .sunPoolCard-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 10px;
      .sunPoolCard-header-l {
        display: flex;
        align-items: center;
        justify-content: flex-start;
      }
    }
    .sunPoolCard-balance-m {
      display: block;
    }
    .sunPoolCard-balance {
      margin-top: 9px;
    }
    .sunPoolCard-balance-v3-top {
      margin-bottom: 9px;
      .sunPoolCard-balance-inner-r {
        display: none;
      }
    }
    .sunPoolCard-balance-v3-btm {
      .sunPoolCard-balance-inner-l {
        display: none;
      }
    }
    .sunPool-action-btn {
      margin-top: 20px;
    }
    .sunPoolBox-submit {
      margin-top: 20px;
      button {
        height: 40px !important;
        border-radius: 6px !important;
      }
    }
  }

  .sunPoolBoxV3 {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      inset: 0;
      padding: 1px;
      border-radius: 10px;
      background: linear-gradient(181.14deg, rgba(255, 255, 255, 0.2) 25.9%, rgba(255, 255, 255, 0.05) 76.35%);
      -webkit-mask: linear-gradient(#fff, #fff) content-box, linear-gradient(#fff, #fff);
      -webkit-mask-composite: xor;
      mask-composite: exclude;
      z-index: 0;
      pointer-events: none;
    }
    .sunPoolBox-left,
    .sunPoolBox-right {
      &::before {
        display: none;
      }
    }
  }

  .sunPool-firstCreateInfo {
    margin-top: 20px;
    .sunPool-firstCreateInfo-title {
      font-size: 14px;
      line-height: 20px;
    }
    .sunPool-firstCreateInfo-desc {
      margin-top: 10px;
      font-size: 12px;
      line-height: 18px;
    }
  }
}
