@import '../../../../../assets//css/theme';
@import '../../../../../assets/css/var';

.price_title {
  font-size: 16px;
  font-family: Poppins;
  font-weight: 600;
  color: $color-text-default;
  line-height: 22px;
  margin-bottom: 10px;
  margin-top: 30px;
  @include mobile-only {
    margin-top: 24px;
    font-size: 14px;
  }
}

.price_number {
  font-size: 20px;
  font-family: Poppins-Bold;
  color: #fff;
  line-height: 24px;
  margin-right: 8px;
  word-break: break-all;
}

.price_symbol {
  font-family: Poppins;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #a29cbc;
}
