@import '../../../../../assets/css/var.scss';
@import '../../../../../assets//css/theme';

@mixin greyText {
  font-family: Poppins;
  font-size: 12px;
  color: #9e9abc;
  text-align: left;
}

.v3-order-modal.ant-modal {
  .ant-modal-content {
    border-radius: 10px;
  }
  .ant-modal-footer {
    border: none;
    text-align: center;
  }
}
.v3-order-modal-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  line-height: 1em;
  padding: 10px 0;
  color: #333;
}
.v3-order {
  &-title {
    font-size: 12px;
  }
  &-grey-text {
    @include greyText;
    line-height: 18px;
    margin: 10px 0 20px;
  }
  &-info {
    font-size: 14px;
    background: rgba(193, 160, 255, 0.3);
    border-radius: 6px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    padding: 16px 20px;
    font-weight: 600;
    color: #decdff;
    line-height: 21px;
    text-align: left;
  }
  &-logo {
    width: 16px;
    height: 16px;
    margin-right: 10px;
    vertical-align: middle;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }
  &-ver-mid {
    vertical-align: middle;
    color: #666;
  }
}
//v3-order
.v3-order {
  &-success {
    text-align: center;
  }
  &-success-icon {
    width: 80px;
    margin-top: 20px;
  }
  &-title-green {
    font-size: 20px;
    color: #16c378;
    margin: 24px 0 12px;
  }
  &-flex {
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  &-add-value {
    font-size: 20px;
    color: #333333;
    letter-spacing: 0;
    text-align: right;
    display: inline-block;
    word-break: break-all;
  }
  &-add-token {
    font-size: 12px;
    color: #999;
    margin-top: 9px;
    margin-left: 3px;
  }
  &-plus {
    font-size: 14px;
    color: #5915e1;
    margin: 0 3px;
  }
  &-supplyDescTitle {
    margin-top: 14px;
    opacity: 0.8;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #999999;
  }
}
.v3-errorMsg {
  font-size: 14px;
  color: #ff8e18;
  background: url(../../assets/images/failed.svg) left no-repeat;
  background-size: 13px;
  padding-left: 18px;
  text-align: left;
  margin-bottom: 10px;
}
