@import '../../../../../assets/css/var.scss';
@import '../../../../../assets/css/theme';

.current-price-wrap {
  margin-bottom: 30px;
}
.current-price-desc {
  font-family: Poppins;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #a29cbc;
}
.current-token-price {
  font-size: 14px;
  font-family: Poppins;
  color: $color-text-default-7;
  line-height: 18px;
  margin: 20px 0 12px;
  display: flex;
  align-items: center;
  .v3-coin-logo-medium {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }
}
.current-price-symbol {
  font-size: 15px;
  font-weight: bold;
  color: $color-text-default;
  margin-right: 6px;
}

#root .v3-current-price-input {
  font-family: Poppins-Bold;
  width: 100%;
  background: #26193d;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #fff;
  height: 40px;
  font-size: 16px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  &:hover {
    border-color: rgba(255, 255, 255, 0.3);
    background: #33254e;
  }
  &::placeholder {
    color: #6c6686;
  }
}
#root .v3-current-price-input:focus {
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.v3-price-suffix {
  display: flex;
  align-items: center;
  &-img {
    width: 20px;
    height: 20px;
    margin-right: 6px;
  }
  &-symbol {
    font-size: 20px;
    font-weight: bold;
    color: #fff;
  }
}
.mobile-content {
  .current-price-desc {
    font-size: 12px;
  }
}

@include mobile-only {
  .current-price-wrap {
    margin-bottom: 20px;
  }
  .current-price-desc {
    font-size: 12px;
    line-height: 20px;
  }
}
