@import '../../../assets/css/var';
@import '../../../assets/css/mixins';

.exchangePool {
  .wrapper {
    margin: 0 auto;
    width: 1200px;
  }
  .header {
    padding: 20px 0 30px;
    .back {
      margin-bottom: 40px;
    }
    .title {
      .titleInner {
        display: flex;
        align-items: center;
        .titleText {
          margin-right: 10px;
          font-family: Poppins;
          font-weight: 600;
          font-size: 30px;
          line-height: 45px;
          color: #fff;
        }
      }
    }
  }
  .tab {
    position: relative;
    margin-bottom: 30px;
    .setting {
      display: none;
    }
    & > div > div > div > div {
      font-size: 20px;
    }
  }
  .tab_add {
    width: 700px;
    &::after {
      position: absolute;
      content: '';
      left: 0;
      right: 0;
      height: 1px;
      background: linear-gradient(90deg, #6c6686 0%, #0a0118 100%);
    }
  }
}

@include mobile-only {
  .exchangePool {
    .wrapper {
      padding: 0 15px;
      margin: 0 auto;
      width: 100%;
    }
    .header {
      // padding: 10px 0 20px;
      padding: 0 0 20px;
      .back {
        margin-bottom: 20px;
      }
      .title {
        .titleInner {
          .titleText {
            font-size: 20px;
            line-height: 30px;
          }
        }
      }
    }
    .header2 {
      margin-bottom: -44px;
    }
    .tab {
      display: flex;
      justify-content: space-between;
      margin-bottom: 10px;
      & > div > div > div > div {
        font-size: 16px;
      }
      .setting {
        display: block;
      }
    }
    .tab_add {
      width: 100%;
      &::after {
        width: 100%;
        bottom: 0;
        width: 260px;
      }
    }
  }
}
