@import '../../../../../assets/css/theme';
@import '../../../../../assets/css/mixins';

.wrapper {
  position: relative;
  font-family: Poppins;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #a29cbc;
}
.info_range_line {
  display: inline-flex;
  margin-top: 20px;
}

.info_tilte {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #a29cbc;
  > span {
    margin-right: 10px;
  }
}

.info_crt_price {
  // display: flex;
  // flex-wrap: wrap;
  // align-items: center;
  margin-top: 15px;
  font-size: 14px;
  font-family: Poppins;
  font-weight: 700;
  color: #a29cbc;
  line-height: 30px;
  > span {
    vertical-align: middle;
    &:first-child {
      margin-right: 10px;
      font-weight: 700;
      white-space: nowrap;
    }
  }
  :global {
    .v3-hover-text {
      margin-right: 8px;
      word-break: break-all;
      @include mobile-only {
        font-size: 16px;
      }
    }
  }
}

.info_price_box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background: #26193d;
  border-radius: 10px;
  padding: 20px 0;
  margin-top: 10px;
  .info_price_box_item {
    position: relative;
    flex: 1;
    text-align: center;
    &:nth-child(2) {
      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 1px;
        top: 9px;
        bottom: 9px;
        background: #33254e;
      }
    }
  }
  .info_price_box_title {
    font-family: Poppins;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #a29cbc;
  }
  .info_price_box_value {
    margin-top: 16px;
    font-family: Poppins-Bold;
    font-weight: 700;
    font-size: 20px;
    min-height: 30px;
    line-height: 30px;
    color: #fff;
  }
  .info_price_box_desc {
    margin-top: 10px;
    font-family: Poppins;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #6c6686;
  }
}

@include mobile-only {
  .info_tilte {
    font-size: 12px;
    & > span {
      margin-right: 6px;
    }
  }
  .info_range_line {
    margin-top: 16px;
  }
  .info_crt_price {
    margin-top: 12px;
    line-height: 18px;
    font-size: 12px;
    line-height: 24px;
    .v3-hover-text {
      font-size: 16px;
    }
  }
  .info_price_box {
    display: block;
    margin-top: 16px;
    padding: 0;
    background: transparent;
    .info_price_box_item {
      display: flex;
      align-items: center;
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
      .info_price_box_title {
        font-size: 12px;
        line-height: 18px;
      }
      .info_price_box_value {
        margin-top: 0;
        margin-left: auto;
        margin-right: 4px;
        font-size: 14px;
        min-height: 18px;
        line-height: 18px;
      }
      .info_price_box_desc {
        margin-top: 0;
        font-size: 12px;
        line-height: 18px;
      }
    }
  }
}
