@import '../../../../../assets/css/var.scss';
@import '../../../../../assets//css/theme';

.safe_tips_text {
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: $color-text-default;
}

.warningTips {
  position: relative;
  padding-left: 28px;
  padding-right: 19px;
  font-family: Poppins;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #fff;
  word-break: break-word;
  b {
    font-family: Poppins-Bold;
    font-weight: 900;
    color: #ffc400;
  }
  .icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 19px;
    height: 19px;
    svg {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .safeBtn {
    display: inline-block;
    font-family: Poppins-Bold;
    font-weight: 700;
    color: #bfabff;
    cursor: pointer;
  }
}

@include mobile-only {
  .warningTips {
    line-height: 20px;
  }
}
